@import url(https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

header{
    display: grid;
    justify-items: center;
}

.NavBar {
    display: flex;
    justify-content: space-around;
    font-size: 2vw;
    font-family: "Poppins";
    position: fixed;
    width: 96%;
    top:2px;
    z-index: 10;
    border-radius: 10px;
}

.NavBar-Dark{
    border: black solid thick;
    background: rgb(200, 200, 200);
}
.NavBar-Purple{
    border: rgb(0, 0, 50) solid thick;
    background: rgb(50, 0, 50);
    color: white;
}
.NavBar-Light{
    border: whitesmoke solid thick;
    background: white;
    border-radius: 0;
}

.NavLink, .off, a, span{
    color: black;
    flex-grow: 1;
    cursor: pointer;
    flex-shrink: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}

.off{
    color: rgb(0, 0, 50);
}
.off:hover{
    color: lightblue;
}

h1 {
    color: white;
    font-size: 3vw;
    font-family: "Permanent Marker";
}

b{
    color: rgb(188,0,45);
    font-size: 1.5vw;
}

.NavLink:hover, #Cart a:hover{
    color: white;
    background-color: black;
}

.mobileOn{
    display: none;
}

.NavLink-Purple{
    color:white;
    background-color: rgb(50, 0, 50);
    border-radius: 5px;
}
.NavLink-Dark{
    color:black;
    background-color: rgb(200, 200, 200);
    border-radius: 5px;
}
.NavLink-Light{
    color: black;
    background-color: white;
    border-radius: 5px;
}

@media (pointer:none),(pointer:coarse) {
    .mobileOff{
        display: none;
    }
    .mobileOn{
        display: flex;
    }
    a, span{
        font-size: 1rem;
    }
    .dropdown button{
        align-self: flex-end;
        color: white;
        background-color: blue;
        border:blue solid thick;
        border-radius: 5px;
        width: 30vw;
    }
    .dropdown-menu{
        border-radius: 10px;
        width: 30vw;
    }
    .DropDown-Dark{
        background-color: rgb(200, 200, 200);
        border: black solid thick;
    }
    .DropDown-Light{
        background-color: white;
        border: black solid thick;
    }
    .DropDown-Purple{
        background-color: rgb(50, 0, 50);
        border: black solid thick;
    }
}

.Main-Dark{
    background-color: darkcyan;
}
.Main-Light{
    background-color: white;
}
.Main-Purple{
    background-color: black;

}

.Main{
    width: 100%;
    padding-top: 10vw;
}
.portfolioContainer{
        display: grid;
        flex-wrap: wrap;
        justify-content: center;
        grid-gap: 5vw;
        gap: 5vw;
        grid-template-columns: auto auto auto;
}
.portfolioItems{
    width: 20vw;
    height: 20vw;
    color: white;
    flex-grow: 0;
    border-radius: 1vw;
    font-size: 2vw;
    text-align: center;
    box-shadow: 0vw 0vw 1vw 0.1vw rgb(0 0 0 / 50%);
}

.portfolioItems-Purple{
    box-shadow: 0vw 0vw 1vw 0.1vw rgb(255 255 255 / 50%);
}
